/*----------------------------------------*/
/*  10. Team CSS
/*----------------------------------------*/

.team-slider {
	display: flex;
	flex-direction: column-reverse;
}

.static-team {
	height: 600px;
	max-height: 600px;
	padding: 50px 40px 55px;
	background-color: $white;
	border: 1px solid #e8e8e8;
	border-radius: 5px;
	box-shadow: 0px 0px 40px 0px rgba(51, 51, 51, 0.1);
	text-align: center;

	// Responsive
	@media #{$large-mobile} {
		padding: 30px 25px 35px;
	}
	.team-image {
		height: 300px;
		// max-width: 350px;
		border-radius: 50%;
		overflow: hidden;
		position: relative;
		margin: auto;
		img {
			height: 100%;
			border-radius: 100%;
			overflow: hidden;
			background-repeat: no-repeat;
			background-size: 100% 100%;
		}
	}

	.thumb {
		position: relative;
		.overlay {
			position: relative;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			border-radius: 50%;
			padding-bottom: 0px;
			overflow: hidden;
			padding: 0;
			opacity: 0;
			visibility: hidden;
			background-blend-mode: multiply;
			background-size: cover;
			background-color: $primary;
			transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
		}

		.social-icon {
			position: absolute;
			bottom: 33px;
			left: 0;
			padding: 0 30px;
			font-size: 0;
			opacity: 0;
			visibility: hidden;
			transform: translateY(-5px);
			transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
			right: 0;
			margin: 0 -15px;
			li {
				margin: 0;
				display: inline-block;

				a {
					transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
					display: inline-block;
					font-size: 18px;
					color: #222;
					width: 40px;
					height: 40px;
					line-height: 40px;
					border-radius: 50%;
					background: $white;
					margin: 7px;
					&:hover {
						background-color: $primary;
						color: $white;
					}
				}
			}
		}
	}

	&:hover {
		.thumb {
			img {
			}

			.overlay {
				opacity: 0.5;
				visibility: visible;
			}

			.social-icon {
				opacity: 1;
				visibility: visible;
				transform: translateY(0);
			}
		}
	}

	.team-content {
		margin-top: 25px;
		margin-bottom: 10px;

		p {
			font-size: 18px;
			font-weight: 500;
			line-height: 27px;
			color: $secondary;
			margin-bottom: 0;
		}
	}
	.author-info {
		display: inline-block;
		.cite {
			.name {
				display: block;
				font-size: 17px;
				line-height: 1.3;

				margin-bottom: 0;
				font-weight: 400;
				text-transform: uppercase;
				color: $primary;
			}
			.position {
				display: block;
				margin-top: 10px;
				font-size: 15px;
				color: $secondary;
			}
		}
	}
}
