/* 12.05 Contact CSS */
.contact-section {
  position: relative;

  & .shape {
    position: absolute;
    z-index: -1;

    &-1 {
      top: 0;
      left: 0;
      right: 0;
      text-align: center;
      margin: 0 auto;
    }
  }

  // Responsive
  @media #{$large-mobile} {
    & .shape {
      &-1 {
        width: 250px;
      }
    }
  }
}
.contact-info {
  display: flex;
  align-items: flex-start;
  padding: 45px 30px 50px;
  border-radius: 5px;
  background-color: $white;
  box-shadow: 0 0 40px rgba(51, 51, 51, 0.1);
  transition: $transition;
  // Responsive
  @media #{$large-mobile} {
    padding: 25px 30px 30px;
    flex-wrap: wrap;
  }
  &.info-light {
    padding: 25px 0;
    background-color: transparent;
    box-shadow: none;
    border-radius: 0;
    border-bottom: 1px solid $white;
    &:last-child {
      border-bottom: 0 none;
      padding-bottom: 0;
    }
    .icon {
      svg {
        & path {
          stroke: $white;
        }
      }
      // Responsive
      @media #{$desktop-device} {
        margin-right: 20px;
      }
      @media #{$large-mobile} {
        margin-right: 15px;
      }
      @media #{$extra-small-mobile} {
        margin-right: 0px;
        margin-bottom: 15px;
      }
    }
    .info {
      .title {
        color: $white;
      }
      .info-text {
        color: $white;
        strong {
          color: $white;
        }
      }
    }
  }
  &.ct-info-two {
    padding: 25px 0;
    background-color: transparent;
    box-shadow: none;
    border-radius: 0;
    border-bottom: 1px solid #e7e7e7;
    &:last-child {
      border-bottom: 0 none;
      padding-bottom: 0;
    }
    .icon {
      svg {
        & path {
          stroke: $primary;
        }
      }
      // Responsive
      @media #{$desktop-device} {
        margin-right: 20px;
      }
      @media #{$large-mobile} {
        margin-right: 15px;
      }
      @media #{$extra-small-mobile} {
        margin-right: 0px;
        margin-bottom: 15px;
      }
    }
  }
  .icon {
    margin-right: 40px;
    // Responsive
    @media #{$desktop-device} {
      margin-right: 20px;
    }
    @media #{$large-mobile} {
      margin-right: 20px;
      margin-bottom: 20px;
    }
    @media #{$extra-small-mobile} {
      margin-right: 30px;
      margin-bottom: 0px;
    }
    svg {
      width: 50px;
      & path {
        stroke: $primary;
      }
    }
  }
  .info {
    .title {
      font-size: 20px;
      line-height: 1;
      font-weight: 400;
      margin-bottom: 15px;
    }
    .info-text {
      font-size: 16px;
      display: block;
      line-height: 27px;
      strong {
        color: $heading-color;
      }
    }
  }
}

.contact-map-area {
  iframe {
    width: 100%;
    height: 500px;
    border: 0 none;
    // Responsive
    @media #{$desktop-device} {
      height: 400px;
    }
    @media #{$tablet-device} {
      height: 350px;
    }
    @media #{$large-mobile} {
      height: 300px;
    }
  }
}

.contact-form-area {
  padding: 70px 90px;
  border-radius: 10px;
  background-color: $white;
  position: relative;
  // Responsive
  @media #{$desktop-device} {
    padding: 50px 30px;
  }
  @media #{$large-mobile} {
    padding: 50px 40px;
  }
  @media #{$extra-small-mobile} {
    padding: 30px 20px;
  }
  & .shape {
    position: absolute;
    z-index: -1;

    &-1 {
      top: auto;
      left: auto;
      bottom: -45px;
      right: -60px;
    }
  }

  // Responsive
  @media #{$laptop-device} {
    & .shape {
      &-1 {
        right: -5px;
      }
    }
  }
  @media #{$desktop-device} {
    & .shape {
      &-1 {
        right: -5px;
      }
    }
  }
  @media #{$tablet-device} {
    & .shape {
      &-1 {
        right: -5px;
      }
    }
  }
  @media #{$large-mobile} {
    & .shape {
      &-1 {
        right: -5px;
      }
    }
  }
}

.custom-file-upload {
  // border: 1px solid #ccc;
  // display: inline-block;
  // padding: 6px 12px;
  cursor: pointer;
  width: 100%;
  min-height: 56px;
  padding: 15px 20px;
  color: #1292ee;
  border: 1px solid #f5f5f5;
  border-radius: 5px;
  outline: none;
  background-color: #f5f5f5;
  border: thin #748494 dotted;
  text-align: center;
  font-style: normal;
  &:hover {
    border: thin #1292ee dotted;
  }
  .custom-file-name {
    display: inline;
    padding: 15px;
  }
}

.shape-animate {
  position: relative;
  & .shape {
    position: absolute;
    z-index: -1;

    &-1 {
      bottom: -45px;
      right: -40px;
      text-align: center;
      margin: 0 auto;
    }
  }

  // Responsive
  @media #{$laptop-device} {
    & .shape {
      &-1 {
        bottom: -20px;
        right: 0px;
      }
    }
  }
  @media #{$desktop-device} {
    & .shape {
      &-1 {
        bottom: -20px;
        right: 0px;
      }
    }
  }
  @media #{$tablet-device} {
    & .shape {
      &-1 {
        display: none;
      }
    }
  }
  @media #{$large-mobile} {
    & .shape {
      &-1 {
        display: none;
      }
    }
  }
}

.recaptcha-submit-container {
  display: flex;
  // Responsive

  @media #{$large-mobile} {
    display: block;
  }
  @media #{$extra-small-mobile} {
    display: block;
  }
}
.recaptcha-submit-container .recaptcha-container .recaptcha {
  text-align: left;
  flex: 50%;
  @media #{$large-mobile} {
    transform: scale(1);
    text-align: left;
    display: block;
  }
}

.recaptcha-submit-container .submit-btn {
  text-align: right;
  flex: 50%;
  @media #{$large-mobile} {
    text-align: right;
    padding-top: 11px;
    display: block;
  }
  @media #{$extra-small-mobile} {
  }
  button {
    margin-top: 10px;
  }
}
